.app-container {
  text-align: center;
  font-family: Arial, sans-serif;
}

.app-header {
  margin-bottom: 20px;
}

.app-title {
  font-size: 1.5em;
  margin-bottom: 10px;
}

.app-subtitle {
  background-color: #b58d2b; /* Gold background color */
  color: black; /* Black text color */
  padding: 10px;
  border-radius: 15px;
  display: inline-block;
  margin-bottom: 20px;
}

.countdown-timer {
  font-size: 1.5em;
  margin-bottom: 20px;
  color: white;
}

.task-form {
  margin-bottom: 20px;
  color: white; /* Set text color to white */
}

.task-form table {
  width: 50%;
  table-layout: auto;
  margin-bottom: 20px;
  border-collapse: collapse;
}

.task-form th, .task-form td {
  padding: 10px;
  border: 1px solid #ddd;
  text-align: left;
}

.questions {
  margin-bottom: 20px;
}

.question {
  margin-bottom: 10px;
}

.question label, 
.question input {
  color: white; /* Ensure inputs also have white text */
}

.leaderboard {
  margin-top: 20px;
}

.leaderboard h3 {
  margin-bottom: 10px;
}

.leaderboard table {
  width: 100%;
  border-collapse: collapse;
}

.leaderboard th, .leaderboard td {
  padding: 10px;
  border: 1px solid #ddd;
  text-align: left;
  color: white;
}

.leaderboard th {
  background-color: black;
  color: white;
}

.submit-task-button {
  margin-right: 10px; /* Add right margin */
}

.clear-tasks-button {
  margin-left: 10px; /* Add left margin */
}

button {
  margin-top: 20px;
  padding: 10px 20px;
  background-color: black;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #0056b3;
}

.answer-questions-button {
  background-color: #b58d2b; /* Gold background color */
  color: black; /* Black text color */
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  font-size: 1em;
  cursor: pointer;
  margin: 20px 0;
  width: 100%; /* Make the button full width of its container */
}

.answer-questions-button:hover {
  background-color: #d4af37; /* Slightly lighter gold on hover */
}

.leaderboard-container {
  display: flex;
  flex-direction: column;
  width: 70%; /* Increase the width of the container */
  margin: 0 auto; /* Center the container */
}

.leaderboard-container .answer-questions-button, 
.leaderboard-container .leaderboard {
  width: 100%; /* Ensure the button and leaderboard take the full width of the container */
}
