/* CountdownTimer.css */
.countdown-timer {
    font-family: 'Courier New', Courier, monospace; /* Use a digital clock style font */
    font-size: 5.1em; /* Increased the font size by one point */
    background-color: black; /* Set the background color to black */
    padding: 20px; /* Add padding around the clock */
    border: 5px solid white; /* Add a thick white border */
    border-radius: 10px; /* Optional: Add rounded corners */
    display: inline-block; /* Ensure the clock is inline-block */
    text-align: center; /* Center the text */
}

.countdown-timer span {
    color: red; /* Ensure the digits color is set to red */
}
