/* Leaderboard.css */
.leaderboard-container {
  margin: 20px 0;
  padding: 10px;
  background-color: #000;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.leaderboard {
  width: 100%;
  border-collapse: collapse;
}

.leaderboard th,
.leaderboard td {
  padding: 10px;
  border: 1px solid #ddd;
  text-align: left;
  color: #fff; /* Ensure text color is white */
}

.leaderboard th {
  background-color: #b58d2b; /* Gold color for the header row */
  color: #000; /* Black text color for the header row */
}

.leaderboard tr {
  color: #fff; /* Ensure text color is white for all rows */
}

.leaderboard tr:nth-child(odd) {
  background-color: #000;
}

.leaderboard tr:nth-child(even) {
  background-color: #111;
}

.leaderboard tr:first-child {
  background-color: #b58d2b; /* Gold color for the first row */
  color: #000; /* Black text color for the first row */
}

.leaderboard tr:nth-child(2) {
  background-color: #000; /* Second row in black */
}

.leaderboard tr:nth-child(3) {
  background-color: #000; /* Third row in black */
}

.leaderboard tr:nth-child(4) {
  background-color: #c02739; /* Red color for the fourth row */
}

.highlight-current-user {
  font-weight: bold;
  background-color: green !important;
  color: #fff; /* Ensure highlighted text is white */
}

.highlight-reset {
  background-color: red;
  color: white; /* Ensure reset text is white */
}
